  left: 50%;
}

.toggle {
  cursor: pointer;
  background-color: #83d8ff;
  border-radius: 84px;
  width: 90px;
  height: 50px;
  transition: background-color .2s cubic-bezier(.445, .05, .55, .95);
  display: block;
  position: relative;
}

.toggle:before {
  content: "LIGHT";
  color: #00000070;
  font-size: 18px;
  position: absolute;
  top: 15px;
  left: -70px;
}

.toggle:after {
  content: "DARK";
  color: #749ed7;
  font-size: 18px;
  position: absolute;
  top: 15px;
  right: -70px;
}

.toggle__handler {
  z-index: 1;
  background-color: #ffcf96;
  border-radius: 50px;
  width: 44px;
  height: 44px;
  transition: all .4s cubic-bezier(.68, -.55, .265, 1.55);
  display: inline-block;
  position: relative;
  top: 3px;
  left: 3px;
  transform: rotate(-45deg);
  box-shadow: 0 2px 6px #0000004d;
}

.toggle__handler .crater {
  opacity: 0;
  background-color: #e8cda5;
  border-radius: 100%;
  transition: opacity .2s ease-in-out;
  position: absolute;
}

.toggle__handler .crater--1 {
  width: 4px;
  height: 4px;
  top: 18px;
  left: 10px;
}

.toggle__handler .crater--2 {
  width: 6px;
  height: 6px;
  top: 28px;
  left: 22px;
}

.toggle__handler .crater--3 {
  width: 8px;
  height: 8px;
  top: 10px;
  left: 25px;
}

.star {
  background-color: #fff;
  border-radius: 50%;
  transition: all .3s cubic-bezier(.445, .05, .55, .95);
  position: absolute;
}

.star--1 {
  z-index: 0;
  width: 30px;
  height: 3px;
  top: 10px;
  left: 35px;
}

.star--2 {
  z-index: 1;
  width: 30px;
  height: 3px;
  top: 18px;
  left: 28px;
}

.star--3 {
  z-index: 0;
  width: 30px;
  height: 3px;
  top: 27px;
  left: 40px;
}

.star--4, .star--5, .star--6 {
  opacity: 0;
  transition: all .3s 0 cubic-bezier(.445, .05, .55, .95);
}

.star--4 {
  z-index: 0;
  width: 2px;
  height: 2px;
  top: 16px;
  left: 11px;
  transform: translate3d(3px, 0, 0);
}

.star--5 {
  z-index: 0;
  width: 3px;
  height: 3px;
  top: 32px;
  left: 17px;
  transform: translate3d(3px, 0, 0);
}

.star--6 {
  z-index: 0;
  width: 2px;
  height: 2px;
  top: 36px;
  left: 28px;
  transform: translate3d(3px, 0, 0);
}

input:checked + .toggle {
  background-color: #749dd6;
}

input:checked + .toggle:before {
  color: #749ed7;
}

input:checked + .toggle:after {
  color: #fff;
}

input:checked + .toggle .toggle__handler {
  background-color: #ffe5b5;
  transform: translate3d(40px, 0, 0)rotate(0);
}

input:checked + .toggle .toggle__handler .crater {
  opacity: 1;
}

input:checked + .toggle .star--1 {
  width: 2px;
  height: 2px;
}

input:checked + .toggle .star--2 {
  width: 4px;
  height: 4px;
  transform: translate3d(-5px, 0, 0);
}

input:checked + .toggle .star--3 {
  width: 2px;
  height: 2px;
  transform: translate3d(-7px, 0, 0);
}

input:checked + .toggle .star--4, input:checked + .toggle .star--5, input:checked + .toggle .star--6 {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

input:checked + .toggle .star--4 {
  transition: all .3s cubic-bezier(.445, .05, .55, .95) .2s;
}

input:checked + .toggle .star--5 {
  transition: all .3s cubic-bezier(.445, .05, .55, .95) .3s;
}

input:checked + .toggle .star--6 {
  transition: all .3s cubic-bezier(.445, .05, .55, .95) .4s;
}

