@media only screen and (max-width: 391px) {
  .background-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    position: relative;
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(153, 175, 140, 1) 100%);
    justify-content: center;
    align-items: center;
  }
  .background-container-dark {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: #434242;
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(153, 175, 140, 1) 100%);
    justify-content: center;
    align-items: center;
  }

  .page-frame {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .page-frame-dark {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #434242;
  }

  .inside-frame {
    border-style: dashed;
    height: 188px;
    width: 337px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  .head-logo {
    position: fixed;
    top: 20px;
    left: 20px;
    width: 200px;
    height: auto;
  }
  .menu {
    position: fixed;
    top: 20px;
    left: 325px;
    width: 43px;
    height: auto;
  }
  .mobile-submit-button {
    border-radius: 75px;
    width: 275px;
    height: 95px;
    font-size: 25px;
    font-weight: bold;
    color: #ffff;
    background-color: #828282;
    border-style: solid;
  }
  .btn {
    border-radius: 73px;
    height: 110px;
    width: 432px;
    font-size: 45px;
    font-weight: bold;
    background-color: #828282;
    color: #ffff;
    outline: none;
  }



  .simple-frame {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .intro {
    font-size: 28px;
    font-weight: 300;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #c2c2c2;
  }
}

@media only screen and (min-width: 392px) {
  .background-container {
    display: flex;
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: #d9e0d8;
    justify-content: center;
    align-items: center;
  }
  .background-container-dark {
    display: flex;
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: #434242;
    justify-content: center;
    align-items: center;
  }
  .page-frame {
    position: absolute;
    border-style: solid;
    border-width: 3px;
    border-color: #cccccc;
    width: 1464px;
    height: 1000px;
    border-radius: 20px;
    background-color: #f6f6f6;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .page-frame-dark {
    position: absolute;
    border-style: solid;
    border-width: 3px;
    border-color: #cccccc;
    width: 1464px;
    height: 1000px;
    border-radius: 20px;
    background-color: #434242;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .inside-frame {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  .simple-frame {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .btn {
    border-radius: 73px;
    height: 110px;
    width: 432px;
    font-size: 45px;
    font-weight: bold;
    background-color: #828282;
    color: #ffff;
    outline: none;
  }
  .btn-dark {
    border-radius: 73px;
    height: 80px;
    width: 412px;
    font-size: 45px;
    font-weight: bold;
    background-color: #828282;
    color: #ffff;
    outline: none;
  }
  .intro {
    font-size: 28px;
    font-weight: 300;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #c2c2c2;
  }
}
