@media only screen and (max-width: 391px) {
  .background-gradient-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    position: relative;
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0%, rgba(153, 175, 140, 1) 100%);
    justify-content: center;
    align-items: center;
  }
  .btm-separator {
    text-align: center;
    width: 286px;
    font-size: 18px;
    font-weight: 300;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #c2c2c2;
    padding-bottom: 18px;
  }
  .no-btm-separator {
    text-align: center;
    width: 286px;
    font-size: 18px;
    font-weight: 300;
  }
  .pass-form-container {
    display: flex;
    flex-direction: column;
    width: 273px;
  }

  .pass-label {
    font-family: 'Poppins', sans-serif;
    font-size: 22px;
    font-weight: 300;
  }

  .pass-input {
    height: 26px;
    border-color: #c2c2c2;
    border-radius: 5px;
    border-width: 1px;
    width: 100%;
    border: 1px solid #c2c2c2;
    background-color: #f6f6f6;
  }
  .pass-btn {
    margin-top: 50px;
    border-radius: 75px;
    width: 275px;
    height: 95px;
    font-size: 25px;
    font-weight: bold;
    color: #ffff;
    background-color: #828282;
    border-style: solid;
  }
   
  .simple-text-frame {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
}
@media only screen and (min-width: 390px) {
  .btm-separator {
    text-align: center;
    font-size: 28px;
    font-weight: 300;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #c2c2c2;
  }

  .no-btm-separator {
    text-align: center;
    width: 657px;
    font-size: 25px;
    font-weight: 300;
  }

  .btn-frame {
    display: flex;
    border-style: dotted;
  }

  .pass-btn {
    margin-top: 50px;
    border-radius: 75px;
    width: 275px;
    height: 95px;
    font-size: 25px;
    font-weight: bold;
    color: #ffff;
    background-color: #828282;
    border-style: solid;
  }
}
