.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #EFEFEF;
}

.background-black {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #434242;
}

.top-ellipsis {
  position: absolute;
  top: 0;
  left: 0;
  width: 40vw;
  height: 100%;
  background-image: url('../../../public/topLeftEllipsis4.svg');
  background-size: cover;
}
.bottom-ellipsis {
  position: absolute;
  bottom: -20%;
  right: -20%;
  width: 40vw;
  height: 100%;
  background-image: url('../../../public/bottomRightEllipsis5.svg');
  background-size: cover;
}
