.localshirt-footer-links {
    font-size: 25px;
    text-align: center;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 25px;
}

.localshirt-footer-links li {
    display: inline-block;
    margin-right: 10px;
}

.localshirt-footer-links a {
    text-decoration: none;
    color: #99af8c;
}

.localshirt-footer-links a:hover {
    text-decoration: underline;
}

.localshirt-footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}